import moment from "moment";
import React, { useState } from "react";
import { Modal, ModalHeader, Button } from "semantic-ui-react";
import { Avatar, IconButton } from "@material-ui/core";
import { CenterStuff } from "../../../../Person/OthersProfile/profile/Profile";
import { ArrowUpward, ArrowDownward } from "@material-ui/icons";
import { green } from "@material-ui/core/colors";
import url from "../../../../../strings/url";

export const ISMISM = (props) => {
  const { noteDetails } = props;

  const [upVotes, setUpVotes] = useState(noteDetails.upVotes);
  const [downVotes, setDownVotes] = useState(noteDetails.downVotes);
  const [isUpVoted, setIsUpVoted] = useState(props.isUpVoted);
  const [isDownVoted, setIsDownVoted] = useState(props.isDownVoted);
  return (
    <Modal
      open={Boolean(noteDetails)}
      onClose={() => {
        props.onClose();
      }}
      className="comingFromUpP5"
      dimmer="inverted"
    >
      <ModalHeader>
        <div style={{ display: "flex", alignItems: "center" }}>
          <Avatar
            style={{ marginRight: 10 }}
            src={noteDetails.by.profilephoto}
          ></Avatar>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <div
              style={{
                display: "flex",
                alignItems: "center",

                flexGrow: 1,
              }}
            >
              <div>{noteDetails.by.username}</div>
            </div>
            <div style={{ fontSize: 11 }}>
              Posted This Note {moment(noteDetails.postedOn).fromNow()}
            </div>
          </div>
        </div>
        <div style={{ fontSize: 16, color: "grey", marginTop: 10 }}>
          {noteDetails.forSubject}
        </div>
        <div style={{ fontSize: 16, color: "grey" }}>
          {"Topic :: " + noteDetails.topic}
        </div>
      </ModalHeader>
      <Modal.Content>
        <iframe
          src={noteDetails.src.pdfUrl}
          width="100%"
          height="500px"
          style={{ border: "none", outline: "none", boxShadow: "none" }}
          title={noteDetails.title}
        ></iframe>
      </Modal.Content>
      <Modal.Content>
        Upvote
        <IconButton
          disableRipple
          onClick={() => {
            setIsUpVoted(!isUpVoted);
            setUpVotes(!isUpVoted ? upVotes + 1 : upVotes - 1);
            setDownVotes(isDownVoted ? downVotes - 1 : downVotes);
            setIsDownVoted(isDownVoted ? !isDownVoted : isDownVoted);
            handleUpVote(
              localStorage.getItem("AnonymityToken#123"),
              noteDetails._id
            )
              .then()
              .catch();
          }}
        >
          <ArrowUpward
            className={isUpVoted ? "fadeUpToBlue" : "fadeUp"}
            style={{ animation: "none" }}
          ></ArrowUpward>
        </IconButton>
        DownVote
        <IconButton
          onClick={() => {
            setIsDownVoted(!isDownVoted);
            setDownVotes(!isDownVoted ? downVotes + 1 : downVotes - 1);
            setUpVotes(isUpVoted ? upVotes - 1 : upVotes);
            setIsUpVoted(isUpVoted ? !isUpVoted : isUpVoted);
            handleDownVote(
              localStorage.getItem("AnonymityToken#123"),
              noteDetails._id
            )
              .then()
              .catch();
          }}
          disableRipple
        >
          <ArrowDownward
            className={isDownVoted ? "fadeUpToRed" : "fadeUp"}
            style={{ animation: "none" }}
          ></ArrowDownward>
        </IconButton>
        <CenterStuff>
          <div
            style={{
              fontFamily: "poppins",
              fontSize: 12,
              marginRight: 10,
              animation: "none",
              fontWeight: "bold",
            }}
            className={isUpVoted ? "fadeUpToBlue" : "fadeUp"}
          >
            UpVotes.... {upVotes}
          </div>
          <div
            style={{
              fontFamily: "poppins",
              fontSize: 12,
              animation: "none",
              fontWeight: "bold",
            }}
            className={isDownVoted ? "fadeUpToRed" : "fadeUp"}
          >
            DownVotes.... {downVotes}
          </div>
        </CenterStuff>
      </Modal.Content>
      <Modal.Actions>
        <Button
          onClick={() => {
            props.onClose();
          }}
        >
          Close
        </Button>
      </Modal.Actions>
    </Modal>
  );
};

const handleUpVote = (token, id) => {
  const headers = new Headers();
  headers.append("Authorization", token);
  return fetch(`${url}/notes/upVote/${id}`, { method: "GET", headers });
};
const handleDownVote = (token, id) => {
  const headers = new Headers();
  headers.append("Authorization", token);
  return fetch(`${url}/notes/downVote/${id}`, { method: "GET", headers });
};

