import React from "react";
import { connect } from "react-redux";
class MainGameComponent extends React.PureComponent {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
          flexGrow: 1,
          marginTop: 30,
        }}
      >
        <img
          src={require("../../CWorkers.png")}
          alt="nothing"
          style={{ width: 300, height: 300 }}
        ></img>
        <div
          style={{
            margin: 30,
            fontWeight: "bold",
            fontFamily: "poppins",
            fontSize: 18,
          }}
        >
          Under Construction
        </div>
        <div style={{ fontWeight: "bold", fontFamily: "poppins" }}>
          Check Again Later...
        </div>
      </div>
    );
  }
}

export default connect()(MainGameComponent);
